/* RESET */

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #23252c;
  display: flex;
  align-items: center;
  justify-content: center;
}

img {
  display: block;
  width: 100%;
}

/* END RESET */

/* PROPS */
@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;500;700&display=swap");

:root {
  --clr-light-heading: #f5f5f5;
  --clr-light-text: #dcdcdc;
  --clr-background-darker: #1a1b21;
  --clr-background-dark: #23252c;
  --clr-background-footer: #161619;
  --clr-beruf-text: #f3bf99;

  --ff-main: "Inter", sans-serif;
}
/* END PROPS */

/* style starts here */
html {
  font-family: var(--ff-main);
}

p {
  font-size: 0.75rem;
  font-weight: 400;
  line-height: 150%;
}

.App {
  color: var(--clr-light-text);
  background-color: var(--clr-background-dark);
  border-radius: 10px;
  background-color: var(--clr-background-darker);
}
.container {
  background-color: var(--clr-background-darker);
  width: 317px;
  height: 230px;
  padding: 0 2em 2em 2em;
}

/* info */

.Info {
  text-align: center;
}

.mimipic {
  border-radius: 10px 10px 0 0;
  width: 317px;
  height: 317px;
}

.Info > h2 {
  margin-top: 0.7em;
  padding: 0 2em;
  font-size: 1.5em;
}

.Info > h3 {
  font-weight: 400;
  color: var(--clr-beruf-text);
  margin-top: 0.3em;
  font-size: 0.8em;
}

.Info > p {
  padding: 0.6em;
  font-size: 0.6em;
  text-decoration: none;
}
a,
a:hover,
a:active,
a:visited {
  color: var(--clr-light-text);
  text-decoration: none;
  cursor: pointer;
}
a:hover {
  filter: brightness(85%);
}

.mimipic {
  margin-top: 30px;
}

.button_container {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 1em;
}

.button:hover {
  filter: brightness(85%);
}

/* About */

.About {
  margin-top: 2em;
}
.about--title {
  margin-bottom: 0.5em;
}

/* Interests */
.Interests {
  margin-top: 2em;
}
.interests--title {
  margin-bottom: 0.5em;
}

/* footer */
.Footer {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 317px;
  height: 64px;
  background-color: var(--clr-background-footer);
  border-radius: 0px 0px 10px 10px;
}

.icon-footer {
  width: 25px;
  height: 25px;
  margin: 10px;
}
